.productdisplay{
    display: flex;
    margin: 0px 50px;
    gap: 30px;
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}
.productdisplay-left{
    display: flex;
    gap: 17px;
}
.productdisplay-img-list{
    display: flex;
    flex-direction: column;
    gap: 16px;
}
.productdisplay-img-list img{
    width: 80px;
    height: 80px;
    object-fit: cover;
    cursor: pointer;
}
.productdisplay-img{
    width: 320px;
    height: 400px;
}
.productdisplay-main-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.productdisplay-right{
    display: flex;
    flex-direction: column;
    flex: 1;
}
.productdisplay-right h1{
    color: #3d3d3d;
    font-size: 22px;
    font-weight: 700;
    margin: 0 0 12px 0;
}
.productdisplay-right-stars{
    display: flex;
    align-items: center;
    gap: 5px;
    color: #1c1c1c;
    font-size: 16px;
    margin-bottom: 20px;
}
.productdisplay-right-stars img{
    width: 16px;
    height: 16px;
}
.productdisplay-right-price{
    display: flex;
    gap: 20px;
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 15px;
}
.productdisplay-right-price-old{
    color: #818181;
    text-decoration: line-through;
}
.productdisplay-right-price-new{
    color: #ff4141;
}
.productdisplay-right-description{
    color: #656565;
    font-size: 16px;
    line-height: 1.6;
    margin-bottom: 30px;
}

.productdisplay-right-size h1{
    color: #656565;
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 15px;
}
.productdisplay-right-size-list{
    display: flex;
    gap: 20px;
    margin-bottom: 40px;
}
.productdisplay-right-size-list div{
    padding: 12px 18px;
    background: #fbfbfb;
    border: 1px solid #ebebeb;
    border-radius: 3px;
    cursor: pointer;
    font-weight: 500;
}
.productdisplay-right-size-list div:hover{
    background: #ff4141;
    color: white;
    border-color: #ff4141;
}
.productdisplay-right button{
    padding: 15px 30px;
    width: 180px;
    font-size: 14px;
    font-weight: 600;
    color: white;
    background-color: #ff4141;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 4px;
    margin-bottom: 25px;
}
.productdisplay-right button:hover{
    background-color: #e63939;
}
.productdisplay-right-category{
    margin-top: 10px;
    color: #656565;
    font-size: 16px;
}
.productdisplay-right-category span{
    font-weight: 600;
    color: #3d3d3d;
}
    